.chat {
    &--container {
        padding: 0 1rem;
        @include position(fixed, null, 0, 0, 0);
        display: -webkit-flex;
        display: -ms-flex;
        display: flex;
        justify-content: flex-end;
        pointer-events: none;
        z-index: 999; }
    &__tab {
        display: block;
        position: relative;
        background-color: white;
        border-radius: 8px 8px 0 0;
        box-shadow: 0 1px 4px rgba(0, 0, 0, .3);
        width: 284px;
        pointer-events: auto;
        &--wrapper {
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
            align-items: flex-end;
            pointer-events: none; }
        &.collapsed {
            width: 180px;
            .chat__body {
                display: none;
                height: 0px; }
            .chat__headerAvatar {
                @include square(20px); }
            .chat__headerName {
                max-width: 70%; } }
        &.--newMsg {
            .chat__header {
                background-color: map-get($theme-colors, primary);
                color: white;
                animation: chat-newmsg 1.5s linear infinite;
                .chat__headerName {
                    color: white; }
                .close {
                    color: white;
                    text-shadow: none; } }
            &.--lb {
                .chat__header {
                    background-color: $lb-primary;
                    color: white;
                    animation: chat-newmsg--lb 1.5s linear infinite; } } }
        &.--lb {
            .chat__item__message.--sent {
                background-color: lighten($lb-primary, 10%); } } }
    &__header {
        display: -webkit-flex;
        display: -ms-flex;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: nowrap;
        padding: .5rem;
        cursor: pointer;
        background-color: transparent;
        border-radius: 8px 8px 0 0;
        box-shadow: 0 2px 1px rgba(0, 0, 0, .1);
        transition: all .2s ease;
        &:hover {
            background-color: #f2f3f5; }
        .close {
            font-size: 2rem;
            font-weight: 300;
            outline: none;
            padding: 0 3px;
            float: none;
            line-height: .8; } }
    &__headerAvatar {
        display: inline-block;
        @include square(28px);
        border-radius: 100%; }
    &__headerName {
        display: inline-block;
        color: #1c1e21;
        font-weight: 600;
        font-size: 15px;
        vertical-align: middle;
        text-transform: capitalize;
        &:hover {
            text-decoration: underline;
            color: #1c1e21; } }
    &__body {
        position: relative;
        display: -webkit-flex;
        display: -ms-flex;
        display: flex;
        flex-direction: column;
        height: 358px; }
    &__msgs {
        flex-grow: 1;
        overflow-y: auto; }
    &__item {
        position: relative;
        margin-bottom: .5rem;
        &__avatar {
            @include square(28px);
            border-radius: 100%; }
        &__message {
            display: inline-block;
            padding: 6px 10px;
            max-width: 85%;
            border-radius: 1rem;
            font-size: 14px;
            line-height: 1.25;
            &.--received {
                background-color: #f2f3f5;
                float: left;
                &.--file {
                    background-color: transparent !important; } }
            &.--sent {
                background-color: lighten(map-get($theme-colors, primary), 20%);
                color: white;
                float: right;
                &.--file {
                    background-color: transparent !important; } } } }
    &__slideUp {
        display: none;
        @include position(absolute, 0, 0, 34px, 0);
        background-color: rgba(200,200,200, .85);
        animation: chatSlideUp .3s ease;
        opacity: 0;
        transform: translateY(15px);
        &.--show {
            display: block;
            opacity: 1;
            transform: translateY(0px); } } }

@keyframes chatSlideUp {
    from {
        opacity: 0;
        transform: translateY(15px); }
    to {
        opacity: 1;
        transform: translateY(0px); } }


// override form-control style
.form-control.chat__input {
    border: none;
    background-color: transparent;
    box-shadow: none;
    max-height: 40px; }


@keyframes chat-newmsg {
    0% {
        background-color: rgba(map-get($theme-colors, primary), 1); }
    50% {
        background-color: rgba(map-get($theme-colors, primary), 1); }
    51% {
        background-color: rgba(map-get($theme-colors, primary), .85); }
    100% {
        background-color: rgba(map-get($theme-colors, primary), .85); } }

@keyframes chat-newmsg--lb {
    0% {
        background-color: rgba($lb-primary, .9); }
    50% {
        background-color: rgba($lb-primary, .9); }
    51% {
        background-color: rgba($lb-primary, .75); }
    100% {
        background-color: rgba($lb-primary, .75); } }
