$aside-width: 220px;

.asidebar {
    @include position(fixed, null, 0, 0, null);
    height: 100vh;
    width: $aside-width;
    padding-top: $dashboard-header-height;
    background-color: white;
    &__header {
        padding: 12px 16px 6px;
        text-transform: uppercase;
        font-size: 13px;
        font-weight: 600; } }

.contact {
    &__list {
        position: relative;
        overflow-y: auto;
        overflow-x: hidden;
        max-height: calc(100% - 37px); }
    &__item {
        position: relative;
        display: block;
        padding: .5rem 2rem .5rem 1rem;
        color: $text-muted;
        font-size: 13px;
        font-weight: 600;
        &:hover {
            color: map-get($theme-colors, primary);
            background-color: rgba(map-get($theme-colors, primary), .08); }
        &.--lb {
            &:hover {
                background-color: rgba($lb-primary, .08);
                color: $lb-primary; } }
        &.--active {
            &:after {
                content: '';
                @include position(absolute, 17px, 0.75rem, null, null);
                display: inline-block;
                @include square(8px);
                border: 1px solid white;
                border-radius: 100%;
                background-color: map-get($theme-colors, success); } } }
    &__avatar {
        display: inline-block;
        @include square(26px);
        margin-right: 10px;
        vertical-align: -8px;
        border-radius: 100%;
        overflow: hidden; } }
