.sidebar {
    display: flex;
    flex-direction: column;
    @include position(fixed, 0, 0, 0, null);
    width: $sidebar-width;
    background-color: darken(map-get($theme-colors, primary), 10%);
    padding: 1.25rem 0;
    font-size: 1rem;
    color: white;
    margin-top: 108px;
    transform: translateX($sidebar-width);
    z-index: 1020;
    transition: transform .2s ease-out;
    &__menu {
        // overflow-y: auto
 }        // max-height: 60vh
    &__link {
        color: white;
        display: block;
        padding: .75rem 1.5rem .75rem 1.75rem;
        text-transform: uppercase;
        font-size: 14px;
        &:hover, &:focus {
            color: white;
            background-color: rgba(white, .025); } }
    &.--open {
        box-shadow: -3px 10px 25px rgba(0, 0, 0, 0.2);
        transform: translateX(0); }
    &--open {
        overflow-y: hidden;
        .dashboard__main {
            transform: translateX(calc(#{-$sidebar-width} + 11rem)); } }
    &__toggler {
        padding: 0.35rem .5rem;
        line-height: 1;
        background-color: transparent;
        border: none;
        &[data-open="true"] {
            .sidebar__togglerIcon {
                line {
                    &:nth-child(1) {
                        transform: rotate(-42deg); }
                    &:nth-child(2) {
                        transform: translateX(30px); }
                    &:nth-child(3) {
                        transform: rotate(42deg); } } } }
        .sidebar__togglerIcon {
            line {
                transform: rotate(0deg) translateY(0px);
                transition: all .2s linear;
                &:nth-child(1) {
                    transform-origin: top right; }
                &:nth-child(2) {
                    transform: translateX(0px); }
                &:nth-child(3) {
                    transform-origin: bottom right; } } } } }

.sides-overlay {
    display: none;
    @include position(absolute, 0, 0, -22vh, 0);
    background-color: rgba(0,0,0,0.25);
    min-height: 100vh;
    width: 100%;
    z-index: 980;
    opacity: 0;
    transition: $primary-transition; }

body {
    &.sidebar--open {
        .sides-overlay {
            display: block;
            opacity: 1; } } }
