@media (max-width: 575.98px) {
  .navbar-brand img.logo {
    max-height: 42px; }
  .py-section-5 {
    padding: 3rem 0; }
  .body-default {
    background-size: 400px, auto 570px; }
  .header--home {
    padding-bottom: 0;
    background-image: none; }
  .header--internal {
    background-image: url('../img/cb-img/body-particles.png'), url('../img/cb-img/coinbank-body-bg.svg');
    padding-bottom: 0;
    background-size: 400px, auto 312px; }

  .small-orange {
    color: #f7941d !important;
    &.text-light {
      color: #f7941d !important; } }

  .home__hero {
    padding-top: 0;
    margin-top: 0;
    flex-direction: column;
    //min-height: 740px
    &__title {
      font-size: 24px; }

    &__subtitle {
      font-size: 12px; }

    .slick-dots {
      margin-bottom: .5rem; }

    .__mobile_single_banner {
      position: relative;
      height: 18.75vh;
      width: 100vw;

      img {
        object-fit: cover;
        width: 100vw;
        height: 18.75vh; }

      .__single_banner_title {
        position: absolute;
        font-size: 35px;
        font-weight: 700;
        font-family: 'Mukta', sans-serif;
        color: #fff;
        left: 10vw;
        top: 310px;

        .__small_2 {
          display: block;
          font-size: 18px;
          font-family: 'Mukta', sans-serif;
          color: #fff; }

        .__small_1 {
          display: block;
          font-size: 16px;
          font-family: 'Mukta', sans-serif;
          color: #303392;
          margin-bottom: 10px;
          text-shadow: 2px 2px 2px #f7941d; }

        .__small_link {
          display: block;
          font-size: 16px;
          font-family: 'Mukta', sans-serif;
          color: #303392;
          margin-top: 8px; } } } }

  .marketRate {
    background-color: map-get($theme-colors, secondary);
    color: white !important;

    &__img {
      filter: brightness(900%); } }

  .hexagons-grid {
    grid-auto-columns: (100% / 3);
    grid-auto-rows: 115px;
    grid-row-gap: 45px; }

  .hex-slider-img {
    top: -3rem;
    left: 1rem; }
  #card-sendmoney.card {
    left: -4%;
    top: 9rem; }

  .howWeDoIt__title {
    position: relative; }
  .howWeDoIt__subtitle {
    position: relative;
    font-size: 14px;
    line-height: 1.25; }

  .font-mobile-12 {
    font-size: 12px; }
  .font-mobile-14 {
    font-size: 14px; }

  .services__item {
    svg {
      max-height: 42px; }

    h5 {
      font-size: 16px; } }

  .view404 {
      &__title {
          font-size: 48px; }
      &__logo {
        max-height: 50px; } } }

@media (min-width: 576px) and (max-width: 767.98px) {
  .py-section-5 {
    padding: 4rem 0; }
  .body-default {
    background-size: 450px, auto 560px; }
  .header--home {
    padding-bottom: 0;
    background-size: 400px, 160% 310px; }

  .hex-slider-img {
    top: -3rem; }
  #card-sendmoney.card {
    left: 20%;
    top: 3rem; }

  .home__hero {
    padding-top: 0;
    min-height: 680px;
    margin-top: 0;

    &__title {
      font-size: 24px; }

    &__subtitle {
      font-size: 14px; } } }

@media (min-width: 768px) and (max-width: 991.98px) {
  .body-default {
    background-size: 620px, auto 445px; }
  .home__hero {
    padding-top: 4rem;
    min-height: 760px;
    margin-top: -2.5rem;

    &__title {
      font-size: 42px;
      line-height: 1; } }

  .hex-slider-img {
    top: -3rem; }
  #card-sendmoney.card {
    left: 0; }
  #home-slider-dots {
    position: relative;
    bottom: unset;
    right: unset; } }

@media (min-width: 992px) and (max-width: 1199.98px) {
  .body-default {
    background-size: 620px, auto 441px; }
  .header--home {
    background-size: 620px, auto 445px;
    background-position: left -300px, center -275px; }
  .header--internal {
    background-size: 620px, auto 515px; } }

@media (min-width: 1601px) and (max-width: 1919.98px) {
  .header--home {
    background-size: 700px, 120%; }
  .home-login {
    left: 10%; } }

@media (min-width: 1920px) {
  .header--home {
    background-position: left -310px, center -5px;
    background-size: 700px, 101%; }
  .home-login {
    left: 26%; } }

@media (max-width: 767.98px) {
  .card--alpha--onlymob {
    background-color: rgba(white, .7); }
  .border-mob-right-0 {
    border-right: 0 !important; }
  .border-onlymob-right {
    border-right-width: 1px !important;
    border-right-style: solid; }
  .custom-close-modal {
    top: 0; }
  .text-onlymob-primary {
    color: map-get($theme-colors, primary) !important; }
  .topbar {
    background-color: #3948b0; }

  #whatwedo {
    padding-bottom: 6rem; }

  #home-main-navbar {
    &.navbar {
      background-color: map-get($theme-colors, primary); }

    .navbar-toggler {
      border: none; }

    .nav-link {
      border-radius: 0;
      text-align: center;
      padding-left: .5rem;
      padding-right: .5rem; }

    #main-navigation {
      .nav-item:not(:nth-last-child(2)) .nav-link {
        border-bottom: 1px solid rgba(white, .3); } } }

  .navbar-submenu {
    .nav {
      flex-wrap: nowrap; }

    .nav-item {
      flex-grow: 1;
      text-align: center;

      &:not(:last-child) {
        border-right: 1px solid map-get($theme-colors, secondary); } }

    .nav-link {
      background-color: white;
      color: map-get($theme-colors, secondary);
      border: none;
      border-radius: 0;

      &:hover {
        background-color: #f4f4f9; }

      &.active {
        background-color: map-get($theme-colors, secondary);
        border: none;
        color: white; }

      &[aria-expanded="true"] {
        background-color: map-get($theme-colors, secondary);
        color: white; } } }
  #lang-menu {
    ~ .dropdown-menu {
      min-width: unset;
      width: unset;

      .dropdown-item {
        padding: .5rem .75rem;
        text-align: center; } } }

  #legalViews-navigation {
    position: relative; }

  #howwedoit-slider-dots {
    .slick-dots {
      li {
        width: 0px; } } }
  #howwedoit-slider {
    .slick-list {
      overflow: hidden; } }
  #recipients-slider-mobile {
    // show half of next slider item
    .slick-list {
      padding-right: 20%;
      padding-bottom: 1.5rem; } }

  .wallet {
    &__text, &__amount {
      color: map-get($theme-colors, primary); } }

  .footer__list {
    > li:not(.footer__nested-list) {
      display: inline-block;
      border-right: 1px solid white;
      padding: 0 10px; }

    .footer__nested-list {
      ul {
        display: block; }

      li {
        display: inline-block;
        border-right: 1px solid map-get($theme-colors, secondary);
        padding: 0 10px; } } }
  #home-slider-dots {
    position: relative;
    bottom: unset;
    right: unset; }

  .transactionItem {
    &__avatar {
        @include square(42px); }
    &__name {
      font-size: 18px; }
    &__flag {
        vertical-align: -5px; }
    &__amount {
        &--received {
            font-size: 18px; } }
    &__trackingId {
        font-size: 14px; } }

  .walletHistory {
      &__item {
          &__body {
              padding: 10px; }
          &__type {
              @include square(36px);
              svg {
                  @include square(18px); } }
          &__amount {
              font-size: 18px; } } }
  #wallets-nav {
      .nav-link {
          flex-direction: column;
          &:first-child {
              font-size: 14px; } }
      .nav-link-title {
          font-size: 12px; } }

  .dashboard__main {
      padding: 0 0 1.5rem 0;
 }      //padding-top: calc(98px + 1.5rem)

  .admin-dashboard {
      .navbar-brand img {
          max-height: 32px; } }

  .user-dashboard {
    .dashboard__main {
            padding: calc(#{$dashboard-header-height} + 1.5rem) 0rem 1.5rem 0rem; } }

  .avatar-square-fluid {
      width: 120px;
      height: 0;
      padding-bottom: 120px; } }
