.btn {
    &:hover, &.active {
        .button__img {
            filter: brightness(500%); } } }

.navbar:not(.navbar-submenu) {
        .nav-item {
            &.active {
                .nav-link {
                    color: map-get($theme-colors, secondary); } }
            .nav-link {
                color: white;
                border-radius: 10rem;
                font-size: 15px;
                transition: $primary-transition;
                &:hover {
                    background-color: rgba(white, .05);
                    color: white !important; } } } }


#homeNavbar .nav-item.active .nav-link {
    color: map-get($theme-colors, secondary);
    &:hover {
        color: map-get($theme-colors, secondary) !important; } }

.page-item {
    &:last-child, &:first-child {
        .page-link {
            border-radius: 100% !important; } } }
.page-link {
    @include square(32px);
    text-align: center;
    padding: 3px;
    border-radius: 100%;
    user-select: none;
    margin-left: 5px; }

.dropdown {
    .dropdown-menu {
        z-index: 1080;
        animation: dropdown .2s;
        &.dropdown-menu-right {
            &:before {
                content: '';
                @include position(absolute, -$dropdown-top-arrow-size, 1rem, null, null);
                width: 0;
                height: 0;
                border-left: $dropdown-top-arrow-size solid transparent;
                border-right: $dropdown-top-arrow-size solid transparent;
                border-bottom: $dropdown-top-arrow-size solid white; } } } }

button.nav-link {
    background: none;
    border: none;
    cursor: pointer;
    outline: none; }

.datepicker.datepicker-inline table.table-condensed {
    width: 100%; }

#recipients-slider, #payment-slider {
    .slick-list {
        padding-bottom: 1.5rem; }
    .slick-dots {
        top: -2.75rem;
        right: 0;
        bottom: unset;
        text-align: right; }
    li {
        margin: 0; } }

.custom-readonly[readonly] {
    background-color: transparent;
    color: map-get($theme-colors, primary);
    font-size: 1.5rem;
    box-shadow: none;
    padding: 0;
    border-radius: 0; }

footer a {
    color: map-get($theme-colors, primary);
    &:hover {
        color: darken(map-get($theme-colors, primary), 10%); } }

#profile-tabs.btn-group {
    .btn-secondary {
        &.active {
            font-weight: 600;
            color: map-get($theme-colors, primary) !important;
            border-color: transparent transparent map-get($theme-colors, secondary) transparent !important;
            background-color: white !important;
            img {
                filter: none; } }
        img {
            filter: brightness(900%); } } }

#lang-menu {
    ~ .dropdown-menu {
        min-width: unset;
        width: 100%;
        .dropdown-item {
            font-size: 14px;
            padding: 0rem .75rem;
            text-align: right; } } }

.dropdown-submenu {
    position: relative; }
.dropdown-submenu a::after {
    transform: rotate(-90deg);
    position: absolute;
    right: 6px;
    top: .8em; }
.dropdown-submenu .dropdown-menu {
    top: 0;
    left: 100%;
    margin-left: .1rem;
    margin-right: .1rem; }

#recipients-slider-arrows {
    .slick-prev, .slick-next {
        display: inline-block;
        position: relative;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: map-get($theme-colors, secondary);
        margin: 0 .25rem;
        @include square(24px);
        border-radius: 100%;
        line-height: 1.5;
        transform: none;
        transition: $primary-transition;
        &:before {
            opacity: 1;
            font-family: "FontAwesome"; }
        &:hover {
            background-color: darken(map-get($theme-colors, secondary), 10%); }
        &.slick-disabled {
            background-color: #b5b5b5;
            opacity: .5;
            cursor: initial; } }
    .slick-prev:before {
        content: '\f104'; }
    .slick-next:before {
        content: '\f105'; } }

// show slick sliders when page loading finishes!
.slick-slider {
    display: none;
    &.slick-initialized {
        display: block; } }

.admin-dashboard {
    #dashboard-navbar {
        .nav-link {
            display: flex;
            align-items: center;
            height: 42px; } } }

.alert {
    .alert-icon {
        user-select: none;
        pointer-events: none; } }
.alert-dismissible .close {
    font-size: 2rem;
    line-height: .9; }

.form-control__currency {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 3rem;
    color: $text-muted;
    font-size: 15px;
    text-transform: uppercase;
    margin-right: -3rem;
    z-index: 10;
    padding-top: 5px;
    ~ .form-control {
        padding-left: 3rem; } }
