.oChat {
    display: flex;
    flex-direction: column;
    height: 100%;
    max-height: 600px;
    min-height: 500px;
    &__header {
        background-color: lighten(map-get($theme-colors, secondary), 20%); }
    &__body {
        position: relative;
        background-color: lighten(map-get($theme-colors, secondary), 40%);
        overflow-y: auto;
        overflow-x: hidden;
        flex-grow: 2; }
    &__date, &__text {
        color: #87929b;
        text-align: center;
        font-size: 12px; }
    &__tools {
        background-color: lighten(map-get($theme-colors, secondary), 20%);
        &__textarea--wrapper {
            position: relative;
            textarea {
                resize: none; } }
        &__sendBtn {
            @include position(absolute, 0, .5rem, 0, null);
            &:hover {
                background-color: #f4f4f9; }
            &:focus {
                box-shadow: none; } } }
    &__item {
        position: relative;
        &__avatar {
            position: absolute;
            @include square(24px);
            z-index: 10; }
        &__msg {
            position: relative;
            display: inline-block;
            padding: 14px 1rem 12px;
            max-width: 85%;
            line-height: 1.25;
            font-size: 14px;
            font-weight: 400;
            color: #777; }
        &.--received {
            display: flex;
            justify-content: flex-start;
            .oChat__item__msg {
                background-color: map-get($theme-colors, primary);
                color: white;
                border-radius: 5px 1.5rem 1.5rem 1.5rem; }
            .oChat__item__avatar {
                top: -.75rem;
                left: 1rem; } }
        &.--sent {
            display: flex;
            justify-content: flex-end;
            .oChat__item__msg {
                background-color: #fff;
                color: #777;
                border-radius: 1.5rem 5px 1.5rem 1.5rem;
                box-shadow: 1px 1px 8px rgba(150, 150, 150, 0.1); }
            .oChat__item__avatar {
                top: -.75rem;
                right: 1rem; } }
        &.--systemMsg {
            .oChat__item__msg {
                background-color: map-get($theme-colors, secondary);
                color: #FFF; } } } }
