.cWallet {
    position: relative;
    display: block;
    background-image: linear-gradient(180deg, #303392 0%, #2C57C4 100%);
    padding: 1.5rem;
    border-radius: .5rem;
    overflow: hidden;
    transition: all 0.6s cubic-bezier(0.2, 0.8, 0.2, 1) 0s;
    &__text {
        color: rgba(white, .75); }
    &__amount {
        color: rgba(white, 1);
        font-weight: 400;
        line-height: 1; }
    &__flag {
        display: block;
        @include square(32px);
        border-radius: 100%;
        padding: 2px;
        background-color: rgba(white, .5);
        flex-shrink: 0;
        img {
            border-radius: 100%; } }
    // background images
    &__bg {
        &--right {
            @include position(absolute, 0, 0, 0, null);
            height: 100%;
            transition: all 1s cubic-bezier(0.2, 0.8, 0.2, 1) 0s; }
        &--left {
            @include position(absolute, 0, null, null, 0);
            transition: all .9s cubic-bezier(0.2, 0.8, 0.2, 1) 0s; } }
    &__bg {
        &--right, &--left {
            user-select: none;
            pointer-events: none;
            z-index: 0; } }
    // states
    &:hover {
        .btn {
            background-color: rgba(white, .2) !important; } }
    &.cWallet--transition {
        &:hover {
            // box-shadow: 0px 30px 60px rgba(black, 0.35)
            box-shadow: 0px 10px 20px rgba(black, 0.35);
            transform: scale(1.03, 1.03);
            .cWallet__bg {
                &--right {
                    transform: scale(1.25);
                    transform-origin: center; }
                &--left {
                    transform: scale(0.93) translateX(-8px);
                    @include position(absolute, -.5rem, null, null, -.5rem); } }
            .btn {
                background-color: rgba(white, .2) !important; } }
        &:focus {
            .btn {
                background-color: rgba(white, .3) !important; } } }

    // currencies
    &.--usd {
        background-image: linear-gradient(180deg, #303392 0%, #2C57C4 100%); }
    &.--btc {
        background-image: linear-gradient(180deg, #B17604 0%, #D3A122 100%); }

    // overlay
    .overlay {
        @include position(absolute, 0, 0, 0, 0);
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba(black, .5);
        font-weight: 300;
        font-size: 14px;
        color: white;
        cursor: default;
        &:hover {
            .fa {
                animation: overlayShake .5s both; } }

        @keyframes overlayShake {
            from {}
            to {
                transform: translate3d(0, 0, 0); }

            10%, 30%, 50%, 70%, 90% {
                transform: translate3d(-2px, 0, 0); }

            20%, 40%, 60%, 80% {
                transform: translate3d(2px, 0, 0); } } } }
