body {
    position: relative;
    font-family: $PRIMARY_FONT;
    background-color: $body-bg;
    overflow-x: hidden; }

a {
    &:hover {
        text-decoration: none; } }

::-moz-selection {
    color: $selection-color;
    background: $selection-bg; }


::selection {
    color: $selection-color;
    background: $selection-bg; }
