/* Underline Reveal */
.underline-reveal {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px transparent;
    position: relative;
    overflow: hidden;
    &:not(.active) {
        &:before {
            background: rgba(#515151, .4); } } }
.underline-reveal:before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 0;
    right: 0;
    bottom: 0;
    background: map-get($theme-colors, primary);
    height: 3px;
    -webkit-transform: translateY(4px);
    transform: translateY(4px);
    -webkit-transition-property: transform;
    transition-property: transform;
    -webkit-transition-duration: 0.15s;
    transition-duration: 0.15s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out; }
.underline-reveal:hover:before, .underline-reveal:focus:before, .underline-reveal:active:before, .underline-reveal.active:before {
    -webkit-transform: translateY(0);
    transform: translateY(0); }


a.text-muted:hover {
    color: darken($text-muted, 20%) !important; }


.or-middle {
    position: relative;
    font-size: 14px;
    margin: 1rem 0;
    &:before {
        content: '';
        display: inline-block;
        @include position(absolute, 50%, null, null, 0);
        width: 45%;
        height: 1px;
        background-color: #DFE3E8; }
    &:after {
        content: '';
        display: inline-block;
        @include position(absolute, 50%, 0, null, null);
        width: 45%;
        height: 1px;
        background-color: #DFE3E8; } }


.btn-pill {
    border-radius: 10rem; }

.btn-transparent {
    border: none;
    background-color: transparent;
    cursor: pointer;
    outline: none;
    color: gray;
    &:hover {
        color: #333; }
    &:focus {
        outline: none; } }

.btn-rounded {
    border-radius: 100%;
    @include square(50px);
    padding: .5rem;
    flex-shrink: 0; }

.btn-secondary:not(.active) {
    img {
        filter: brightness(1000%); } }

.btn-light.active {
    background-color: map-get($theme-colors, primary) !important;
    border-color: lighten(map-get($theme-colors, primary), 15%) !important;
    &:active:focus {
        background-color: darken(map-get($theme-colors, primary), 10%) !important;
        border-color: darken(map-get($theme-colors, primary), 15%) !important; }

    path {
        fill: #FFFFFF; } }

@keyframes dropdown {
    from {
        opacity: 0;
        margin-top: -4px;
 }        // transform: scale(.9)
    to {
        opacity: 1;
        margin-top: 2px;
 } }        // transform: scale(1)


.custom-close-modal {
    @include position(absolute, -2rem, 0, null, null);
    display: inline-block;
    border: none;
    color: white;
    font-weight: 600;
    background-color: map-get($theme-colors, secondary);
    padding: .25rem 1rem;
    cursor: pointer;
    user-select: none;
    letter-spacing: 1px;
    outline: none;
    transition: $primary-transition;
    &:hover {
        background-color: darken(map-get($theme-colors, secondary), 8%); }
    &:focus {
        outline: none; }
    &:active:focus {
        background-color: darken(map-get($theme-colors, secondary), 10%); } }

.border-right.--strong {
    border-color: map-get($theme-colors, primary) !important; }

.waiting-trader {
    transform: rotate(0deg);
    animation: waiting-trader 1.5s ease-out infinite;
    animation-fill-mode: both; }

@keyframes waiting-trader {
    0% {
        transform: scale(.5) rotate(0deg); }
    50% {
        transform: scale(1) rotate(0deg); }
    51% {
        transform: scale(1) rotate(0); }
    100% {
        transform: scale(.5) rotate(180deg); } }

.va-middle {
    vertical-align: middle; }

.body-bg-color {
    background-color: $body-bg; }

.btn-as-card {
    @extend .cardBank;
    border: none;
    &:focus {
        outline: 0; }
    &.btn-as-card--light {
        box-shadow: none;
        background-color: #fff;
        border: 1px solid rgba(#c4c4c4, .5);
        color: #515151;
        &:hover {
            color: #515151; } } }

.form-control-font-lg {
    font-size: 1.5rem;
    color: #898989;
    line-height: 25px;
    &::placeholder {
        font-size: .9375rem; } }

.modal-animated-img {
    @include position(absolute, -4px, null, null, -5rem);
    z-index: -10; }

.double-input {
    position: relative;
    input {
        &:first-child {
            padding-right: calc(50% + .75rem);
            text-align: center; }
        &:last-child {
            background-color: transparent;
            box-shadow: none;
            border-left: 1px solid lightgray;
            @include position(absolute, 0, 0, 0, 50%);
            width: 50%;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
            z-index: 10;
            text-align: center; } } }

.modal-footer--cta {
    display: block;
    text-align: center;
    padding: 13px 1rem;
    background-color: rgba(map-get($theme-colors, secondary), .4);
    color: map-get($theme-colors, primary);
    border-bottom: 2px solid rgba(map-get($theme-colors, secondary), .8);
    transition: $primary-transition;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    span {
        display: inline-block;
        transition: $primary-transition;
        transform: translateX(0px); }
    &:hover {
        color: map-get($theme-colors, primary);
        background-color: rgba(map-get($theme-colors, secondary), .3);
        span {
            &:first-child {
                transform: translateX(-5px); }
            &:last-child {
                transform: translateX(5px); } } } }

.custom-navbar-toggler {
    img {
        transform: rotate(0);
        transition: $primary-transition; }
    &:not(.collapsed) {
        img {
            transform: rotate(90deg); } } }

.control-date-icon {
