        line-height: 1.5;
        color: #b3b3b3;
        text-align: right; }
    .form-control {
        padding-right: 3.5rem; } }

.input__rightText--usd {
    @extend %inputRightText;
    &:after {
        content: "USD"; } }
.input__rightText--bss {
    @extend %inputRightText;
    &:after {
        content: "BsS"; } }

.text-orange {
    color: #f75828 !important; }

.triangle--toRight {
    position: relative;
    &:before {
        content: '';
        @include position(absolute, 0, null, 0, 0);
