.triangle--toLeft {
    position: relative;
    &:before {
        content: '';
        @include position(absolute, 0, 0, 0, null);
        width: 110vw;
        height: 100%;
        background: url('../../img/landing/triangle-to-left.png') no-repeat left bottom;
        pointer-events: none;
