.avatar-square-fluid {
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    border: 1px dashed $text-muted;
    background: white;
    &.avatar-background {
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat; } }

.box {
    display: block;
