// BOOTSTRAP FUNCTIONS, VARIABLES AND MIXINS
@import "../../node_modules/bootstrap/scss/functions.scss";
@import "../../node_modules/bootstrap/scss/variables.scss";
@import "../../node_modules/bootstrap/scss/mixins.scss";
@import "theme-colors";

// CONSTANTS
$PRIMARY_FONT: 'Mukta', sans-serif;

$selection-color: rgba(white, .9);
$selection-bg: rgba(map-get($theme-colors, secondary), .7);

$body-bg: #f4f4f9;

$box-bg: #F9F9F9;
$box-bg-hover: darken($box-bg, 1.25%);

// TRANSITION
$primary-transition: all .3s ease;

$tbody-white-border-radius: 7px;
$tbody-white-box-shadow: 2px 2px 10px rgba(173, 173, 173, 0.5);

$dropdown-top-arrow-size: 7px;

// DASHBOARD
$dashboard-header-height: 110px;

// SIDEBAR
$sidebar-width: 240px;

// LOCALBITCOINS
$lb-primary: #F58220;

// MIXINS
@mixin position($po, $top, $right, $bottom, $left) {
    position: $po;
    top: $top;
    right: $right;
    bottom: $bottom;
    left: $left; }

@mixin square($square-size) {
    width: $square-size;
    height: $square-size; }
