@import "variables";
@import "global";
@import "overrides";
@import "utilities";
@import "theme-colors";

// components
@import "chat";
@import "oChat";
@import "dashboardContacts";
@import "sidebar";
@import "wallet";

.body-default {
    background-image: url('../img/cb-img/body-particles.png'), url('../img/cb-img/coinbank-body-bg.svg');
    background-repeat: no-repeat, repeat-x;
    background-position: -5% 20px, top center;
    background-size: 700px, auto 480px; }

.header--home {
    @extend .body-default;
    background-position: left -370px, center -275px;
    background-size: 700px, auto 445px;
    position: relative;
    color: white;
    padding-bottom: 4rem;
    z-index: 10; }

.header--internal {
    @extend .header--home;
    background-image: url('../img/cb-img/body-particles.png'), url('../img/cb-img/coinbank-body-bg.svg');
    background-position: left -310px, center -210px;
    padding-bottom: 50px;
    color: white;
    z-index: unset; }

.home-login {
    @include position(absolute, 0, null, null, 3rem);
    .card-body {
        padding-top: 4.5rem; } }

.navbar-brand img.logo {
    max-height: 65px; }

.contact-form {
    background-image: linear-gradient(to bottom, rgba(white,1) 0%, rgba(white,.6) 25%, rgba(white,0) 50%, rgba(white,0) 100%), url('../img/landing/business-team.png');
    background-size: cover;
    background-repeat: repeat-x;
    background-position: center; }

.wallet {
    &__item {
        padding: 0 1.5rem;
        &:not(:last-child) {
            border-right: 1px solid rgba(white, .3); } }
    &__text {
        color: rgba(white, .8); }
    &__amount {
        color: rgba(white, 1);
        font-weight: 400;
        line-height: 1; } }

.page {
    &__title {
        font-size: 26px;
        font-weight: 600; }
    &__subtitle {
        font-weight: 300; } }

.filtering-tabs {
    .nav-link {
        color: rgba(#515151, .6);
        transition: $primary-transition;
        &:hover {
            color: #515151; }
        &.active {
            color: map-get($theme-colors, primary);
            font-weight: 600; } } }

.card--alpha {
    background-color: rgba(white, .6);
    box-shadow: none; }
.card--alpha--primary {
    background-color: rgba(#2e3c97, .7); }

.section-heading {
    border-bottom: 2px solid map-get($theme-colors, primary); }

.table {
    .thead-none {
        background-color: transparent;
        th {
            border-bottom: none !important;
            border-top: none !important; } } }

.tbody-white {
    box-shadow: $tbody-white-box-shadow;
    border-radius: $tbody-white-border-radius;
    td, th {
        background-color: white; }
    tr:first-child {
        th, td {
            border-top: none;
            &:first-child {
                border-top-left-radius: $tbody-white-border-radius; }
            &:last-child {
                border-top-right-radius: $tbody-white-border-radius; } } }
    tr:last-child {
        th, td {
            &:first-child {
                border-bottom-left-radius: $tbody-white-border-radius; }
            &:last-child {
                border-bottom-right-radius: $tbody-white-border-radius; } } } }

.btn-group {
    .btn[class*="btn-outline-secondary"] {
        font-weight: 600;
        &:not(.active) {
            color: map-get($theme-colors, primary);
            border-color: transparent !important; } } }

.ad {
    &__user {
        font-weight: 600; }
    &__header {
        display: -webkit-flex;
        display: -ms-flex;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        flex-wrap: wrap; }
    &__desc {
        line-height: 1.25;
        font-weight: 400; }
    &__waiting-banner {
        display: -webkit-flex;
        display: -ms-flex;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        height: 100%;
        &--wrapper {
            height: calc(100% - 1.5rem); } } }

.nav-avatar {
    position: relative;
    display: inline-block;
    @include square(30px);
    background-color: rgba(white, .2);
    margin-right: .25rem;
    border-radius: 100%;
    // overflow: hidden
    padding: 2px;
    img {
        border-radius: 100%; }
    .user-status {
        @include position(absolute, null, -2px, 0, null); } }

.loader--wrapper {
    position: relative;
    .loader {
        @include position(absolute, 0, 0, 0, 0);
        background-color: rgba(white, .5);
        border-radius: 7px;
        display: -webkit-flex;
        display: -ms-flex;
        display: flex;
        justify-content: center;
        align-items: center;
        display: none; }
    &.--loading {
        .loader {
            display: -webkit-flex;
            display: -ms-flex;
            display: flex; } } }

.login-bg {
    justify-content: flex-end;
    align-items: center;
    background: url('../img/cb-img/bg-login.jpg') no-repeat center/cover;
    height: 100%; }

.home__hero {
    position: relative;
    // background: url('../img/landing/hero-bg.png') no-repeat center / cover
    // min-height: calc(100vh - 260px + 2rem)
    min-height: 525px;
    margin-top: -3.5rem;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: flex-end;
    padding-top: 8rem;
    .form-control-country-big, .custom-select {
        box-shadow: none; }
    .select-appended {
        &:after {
            display: none; } }
    .form-control-country-big {
        padding-left: 0;
        font-size: 1.25rem;
        &::placeholder {
            font-size: 1rem; } }
    .form-control-country-big[readonly] {
        color: map-get($theme-colors, primary);
        background-color: white; }
    &__title {
        font-size: 65px; }
    &__slider {
        @include position(absolute, 0, 0, 0, 0);
        width: 100%;
        height: 100%;
        user-select: text;
        .slider__item {
            position: relative;
            width: 100%;
            height: 100%;
            background: no-repeat center/cover;
            &:before {
                content: '';
                @include position(absolute, 0, 0, 0, 0);
                background-color: rgba(black, .45); }
            &.__no_dark {
                &:before {
                    background-color: rgba(black, 0); } }
            &__title {
                span, img.arrow-right {
                    transition: $primary-transition; }
                &:hover {
                    span {
                        transform: translateX(-5px); }
                    img.arrow-right {
                        transform: translateX(5px); } } } }
        .slick-list, .slick-track {
            height: 100%; } }
    .slick-dots {
        //display: flex
        //justify-content: stretch
        position: relative;
        bottom: unset;
        margin-bottom: 6rem;
        text-align: right;
        li {
            width: 50px;
            height: 50px;
            border: 1px solid #f7941d;
            flex-grow: 1;
            &.slick-active {
                .btn-slider {
                    background-color: map-get($theme-colors, secondary);
                    color: white;
                    hr {
                        border-color: white; }
                    img {
                        filter: brightness(800%); } } } }
        .btn-slider {
            display: inline-block;
            font-weight: 400;
            color: map-get($theme-colors, primary);
            text-align: center;
            vertical-align: middle;
            user-select: none;
            padding: .375rem .75rem;
            font-size: 14px;
            line-height: 1.5;
            background-color: white;
            border-radius: 0;
            transition: $primary-transition;
            &:hover {
                color: map-get($theme-colors, primary); }
            hr {
                border-color: map-get($theme-colors, primary); } } } }

#home-slider-dots {
    position: absolute;
    bottom: 0;
    right: 10vw; }

.hex-slider-img {
    position: absolute;
    top: -15rem;
    pointer-events: none;
    user-select: none; }

#card-sendmoney.card {
    position: absolute;
    left: 30%;
    top: 4rem;
    max-width: 190px;
    border-radius: 2rem;
    margin: 0 auto;
    box-shadow: 6px 6px 25px rgba(36, 36, 48, 0.29);
