    .hex-slider-icon {
        @include position(absolute, -4.8rem, -6rem, null, null); } }

.accordion {
    .card {
        box-shadow: none; }
    .card-header {
        background-color: white;
        border-bottom: 2px solid #c5c7cc;
        cursor: pointer;
        transition: $primary-transition;
        .fa {
            transition: $primary-transition; }
        &[aria-expanded="true"] {
            background-color: #f4f4f9;
            border-bottom-color: transparent;
            .fa {
                transform: rotate(180deg); } }
        &:hover {
            background-color: #f4f4f9; } }
    .card-body {
        background-color: #f4f4f9; } }

select option[value="us"] {
    background-color: red;
    background-image: url('../img/landing/us-flag.png');
    width: 80%;
    height: 45px; }

.mobile-nav {
    position: relative;
    top: -.5rem;
    .nav-link {
