    padding-right: .25rem;
    background-size: 50px 24px; }

.select-appended {
    &:after {
        content: '\f0dc';
        font-family: FontAwesome;
        @include position(absolute, calc(50% - 10px), 0, null, null);
        font-size: 14px;
        pointer-events: none; } }

#login-tabs {
    .btn-secondary {
        font-size: 18px;
        font-weight: 700;
        small {
            font-size: 12px; }
        &.active {
            background-color: white;
            border-color: transparent;
            color: map-get($theme-colors, primary) !important;
            i.fa {
                color: map-get($theme-colors, secondary); }
            span {
                font-weight: 500; }
            small {
                color: map-get($theme-colors, secondary); } } } }

.input-group-prepend {
    i.fa {
        font-size: small; }
    .input-group-text {
        justify-content: center;
        width: 2.5rem; } }

.cardBank {
    &.selective {
        min-height: 131px; }
    display: block;
    position: relative;
    background-color: #fff;
    box-shadow: 0 1px 6px 0 rgba(173, 173, 173, 0.5);
