            line-height: 22px;
            border-radius: 100%;
            box-shadow: 1px 1px 5px rgba(126, 126, 126, 0.5); }
        img {
            filter: brightness(900%); }
        //.cardBank__edit
 }        //  //display: none
    &--light {
        background-color: #efefef;
        box-shadow: none;
        color: #515151;
        &:hover {
            color: #515151; } } }

.inputRadio {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    &__label {
        display: block;
        position: relative;
        background-color: $body-bg;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.13);
        color: map-get($theme-colors, primary);
        font-weight: 600;
        font-size: 14px;
        padding: 13px 1rem 13px 3.5rem;
        border-radius: 3px;
        cursor: pointer;
        &:before {
            content: '';
            display: inline-block;
            font-family: FontAwesome;
            font-size: 12px;
            @include position(absolute, 0, null, 0, 1rem);
            @include square(22px);
            margin: auto 0;
            background-color: #fff;
            color: map-get($theme-colors, primary);
            text-align: center;
            line-height: 22px;
            border-radius: 100%;
            box-shadow: 1px 1px 5px rgba(126,126,126,0.5); }
        &:hover {
            &:before {
                background-color: $body-bg; } } }
    &__content {
        display: none; }
    &:checked {
        ~ .inputRadio__label {
            background-color: map-get($theme-colors, primary);
            color: #fff;
            &:before {
                content: '\f00c';
                animation: inputRadio--label .3s ease both; }
            ~ .inputRadio__content {
                display: block;
                animation: inputRadio--content .5s ease both; } } } }

@keyframes inputRadio--content {
    from {
        opacity: 0; }
    to {
        opacity: 1; } }
@keyframes inputRadio--label {
    from {
        transform: scale(.7); }
    to {
        transform: scale(1); } }

.dynamicInput {
    &--wrapper {
        position: relative; }
    &__delete {
        display: block;
        @include position(absolute, 0, 0, 0, null);
        background: none;
        border: none;
        cursor: pointer;
        color: #9B9B9B;
        font-size: 1.75rem;
        font-weight: 300;
        line-height: 1;
        -webkit-appereance: none;
        outline: 0;
        &:hover {
            color: darken(#9B9B9B, 15%); }
        &:focus {
            outline: 0; } }
    &[readonly] {
        background-color: #efefef;
        box-shadow: none; } }

.subtabs {
    &__item {
        font-weight: 600;
        padding-left: .25rem;
        padding-right: .25rem;
        color: #3d4246;
        opacity: .5;
        filter: grayscale(1);
        transition: $primary-transition;
        &:hover {
            opacity: 1; }
        &.active {
            opacity: 1;
            filter: grayscale(0);
            color: map-get($theme-colors, primary) !important;
            border-bottom: 1px solid map-get($theme-colors, primary); } } }

.inputFile--selected {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: map-get($theme-colors, primary);
    color: white;
    width: 42px;
    height: 100%;
    text-align: center;
    line-height: 38px;
    animation: file-selected .3s ease-in;
    animation-fill-mode: backwards;
    .fa {
        background-color: white;
        color: map-get($theme-colors, primary);
        font-size: .75rem;
        padding: 3px;
        border-radius: 100%;
        box-shadow: 1px 1px 5px rgba(126, 126, 126, 0.5); } }

@keyframes file-selected {
    0% {
        width: 30px;
        transform: scale(.8) rotate(0deg);
        border-radius: 1rem; }
    10% {
        transform: scale(1.1) rotate(-5deg); }
    80% {
        transform: scale(1.1) rotate(-5deg); }
    100% {
        width: 42px;
        transform: scale(1) rotate(0deg);
        border-radius: .25rem; } }

.topbar {
