            height: unset;
            flex-grow: 1;
            margin: 0;
            &.slick-active {
                .btn-slider {
                    background-color: map-get($theme-colors, secondary);
                    color: white; }
                .btnSliderLoader:before {
                    display: block; } }
            &:not(:last-child) {
