        .btn-slider {
            display: inline-block;
            font-weight: 400;
            color: map-get($theme-colors, primary);
            text-align: center;
            vertical-align: middle;
            user-select: none;
            padding: .375rem .75rem;
            font-size: 14px;
            line-height: 1.5;
            background-color: white;
            border-radius: 0;
            transition: $primary-transition;
            &:hover {
                color: map-get($theme-colors, primary); } } }
    .btnSliderLoader {
        position: relative;
        &:before {
            content: '';
